@import "../../../styles/base";
@import "../../../styles/animation.scss";
.blueblob {
	position: absolute;
	left: 0;
	top: 0;
	height: 20em;
	z-index: -1;
}

.pinkblob {
	position: absolute;
	right: 0;
	top: 40vh;
	height: 30em;
	z-index: -1;
}
.welcome {
	padding-top: 2rem;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;

	&_text_section {
		display: flex;
		flex-direction: column;
		padding: 0 3rem;
		margin-top: 2rem;
		width: 35%;
		height: 50%;
	}

	&_text_hello {
		padding-bottom: 1.2rem;
		font-family: "caveat";
		font-size: 55px;
		color: $font-gray;
		font-weight: 500;
		animation: moveInTop 1s;
	}

	&_text_intro {
		font-family: "dm-serif-display", "serif";
		font-size: 20px;
		color: $font-gray;
		font-weight: 500;
		animation: moveInTop 1s;
		width: 420px;
	}

	&_image {
		position: relative;
		height: 350px;
		width: 350px;
		background-color: transparent;
		border-radius: 100%;
		animation: moveInRight 2s;
	}

	img {
		height: 100%;
		animation: moveInRight 2s;
	}
}

@include sm {
	.welcome {
		padding-top: 2rem;
		flex-direction: column;
		flex-flow: column-reverse;

		&_text_section {
			display: flex;
			flex-direction: column;
			padding: 1rem;
			width: 70%;
			height: auto;
			align-items: center;
			justify-content: center;
		}
		&_text_intro {
			text-align: center;
			font-size: 16px;
			animation: moveInTop 1s;
			width: 108%;
		}

		&_text_hello {
			padding-bottom: 1rem;
			font-family: "caveat";
			font-size: 45px;
			color: $font-gray;
			font-weight: 500;
			animation: moveInTop 1s;
		}
		&_image {
			height: 200px;
			width: 200px;
			animation: moveInTop 1s;
		}

		img {
			height: 100%;
			position: absolute;
			animation: moveInTop 1s;
		}
	}
}
